@media only screen and (max-width: 1023px) { .scroll__row_scroll-bar { display: flex; flex-direction: row; flex-wrap: wrap; justify-content: center; } }

.photo_scroll { position: relative; display: inline-block; vertical-align: top; width: 200px; height: 388px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; border-radius: 3.5px; margin-right: 20px; margin-bottom: 38px; }

.photo_scroll::after { position: absolute; top: 0; content: ''; width: 100%; height: 100%; border-radius: 3.5px; background-image: linear-gradient(to bottom, rgba(25, 25, 38, 0) 0%, rgba(25, 25, 38, 0.67) 80%); transition: opacity .3s; opacity: 1; z-index: 2; }

.photo_scroll:hover::after { opacity: 0; }

@media only screen and (max-width: 1023px) { .photo_scroll { width: calc(50% - 10px); }
  .photo_scroll:nth-child(even) { margin-right: 0; }
  .photo_scroll:hover::after { opacity: 1; } }

@media only screen and (max-width: 599px) { .photo_scroll { width: 100%; height: 164px; margin-right: 0; } }

.ps__rail-x:focus, .ps__rail-x:hover, .ps__rail-y:focus, .ps__rail-y:hover { background-color: none; opacity: 1; }

.ps__rail-x, .ps__rail-y { opacity: 1; }

.ps__thumb-x { height: 6px; }

.ps__thumb-x, .ps__thumb-y { background-color: #6aa2ff; cursor: pointer; }

.ps__rail-x:focus > .ps__thumb-x, .ps__rail-x:hover > .ps__thumb-x { height: 6px; background-color: #6aa2ff; }

[data-tabs] { overflow: hidden; }

[data-tabs] [data-tabs-control] { cursor: pointer; }

[data-tabs] [data-tabs-control][disabled], [data-tabs] [data-tabs-control].is-disabled { cursor: not-allowed; user-select: none; }

[data-tabs] [data-tabs-wrapper] { position: relative; }

[data-tabs] [data-tabs-container] { position: absolute; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; visibility: hidden; transition: opacity 0.3s, visibility 0.3s; }

[data-tabs] [data-tabs-container].is-open { position: relative; opacity: 1; visibility: visible; }
